html {
    font-size: $base-font-size;
  }
  
  /**
   * Reset some basic elements
   */
  body, h1, h2, h3, h4, h5, h6,
  p, blockquote, pre, hr,
  dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
  
  }
  
  
  
  /**
   * Basic styling
   */
  body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background-color: $background-color;
    //background-image: $background-image; // custom
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
       -moz-font-feature-settings: "kern" 1;
         -o-font-feature-settings: "kern" 1;
            font-feature-settings: "kern" 1;
    font-kerning: normal;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    overflow-wrap: break-word;
  }
  
  .smallcaps {
    font-variant: small-caps; // custom: not sure why pandoc changes the class for this?
  }
  
  .csl-left-margin {
    margin-right: 5px;
    font-weight: bold;
  }

  /**
   * Set `margin-bottom` to maintain vertical rhythm
   */
  h1, h2, h3, h4, h5, h6,
  p, blockquote, figure,
  ul, ol, dl,
  %vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
  }

  pre, figure.highlight %vertical-rhythm {
    margin-bottom: 0px;
  }

  hr {
    margin-top: $spacing-unit;
    margin-bottom: $spacing-unit;
    height: 1px; //custom
    border: 0px; //custom
    background-color: $brand-color; //custom
  }
  
  /**
   * `main` element
   */
  main {
    display: block; /* Default value of `display` of `main` element is 'inline' in IE 11. */
  }
  
  
  
  /**
   * Images
   */
  img {
    max-width: 100%;
    vertical-align: middle;
  }

  /**
   * Figures
   */
  figure > img {
    display: block;
  }
  
  figcaption {
    font-size: $small-font-size;
    text-align: center;
  }
  
    //custom figure container break out with 3images
      figure.wide {
        position: center;
        display: inline-block;
      }
    @media screen and (min-width: 1100px) {
      figure.wide {
        margin-left: -35vw;
        margin-right: -35vw;
        width: 70vw;
      }
    }
    @media screen and (min-width: 1500px) {
      figure.wide {
        margin-left: -30vw;
        margin-right: -30vw;
        width: 60vw;
      }
    }
    @media screen and (min-width: 2000px) {
      img.wide3 {
        margin-left: -25vw;
        margin-right: -25vw;
        width: 50vw;
      }
    }


  /**
   * Lists
   */
  

  ul, ol {
    margin-left: $spacing-unit;
  }
 
  li {
    > ul,
    > ol {
      margin-bottom: 0;
    }
  }
  
  
  
  /**
   * Headings
   */
  h1, h2, h3, h4, h5, h6 {
    font-weight: $base-font-weight;
  }
  
  
  
  /**
   * Links
   */
  a {
    color: $link-base-color;
    text-decoration: none;
  
    &:visited {
      color: $link-visited-color;
    }
  
    &:hover {
      color: $link-hover-color;
      text-decoration: underline;
    }
  
    .social-media-list &:hover {
      text-decoration: none;
  
      .username {
        text-decoration: underline;
      }
    }
  }
  
  
  /**
   * Blockquotes
   */
  blockquote {
    color: $brand-color;
    border-left: 4px solid $border-color-01;
    padding-left: $spacing-unit / 2;
    @include relative-font-size(1.125);
    font-style: italic;
  
    > :last-child {
      margin-bottom: 0;
    }
  
    i, em {
      font-style: normal;
    }
  }
  
 
  /**
   * Code formatting
   */
  pre,
  code {
    font-family: $code-font-family;
    font-size: 0.9375em;
    border: 1px solid $code-background-color; //custom -- $border-color-01
    border-radius: 1px;
    background-color: $code-background-color;
  }
  
  code { //inline highlighter-rouge
    padding: 1px 1px;
    border: 1px solid $border-color-01;
    border-radius: 1px;
  }
  
  //custom
  pre {
    padding: 3px 3px;
    overflow-x: auto;
  
    > code {
      border: 3px;
      padding-right: 0px;
      padding-left: 0px;
    }
    .lineno{ //custom
      border: 0px;
      color: burlywood;
    }

  }
  
  .highlight {
    border-radius: 1px;
    background: $code-background-color;
    @extend %vertical-rhythm;
  
    .highlighter-rouge & {
      background: $code-background-color;
    }
    // custom: wrapping for long lines of code; does not work with line numbers
    //pre{
    //  white-space: pre-wrap;
    //  }
  }
  
  
  
  /**
   * Wrapper
   */
  .wrapper {
    max-width: calc(#{$content-width} - (#{$spacing-unit}));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit / 2;
    padding-left: $spacing-unit / 2;
    @extend %clearfix;
  
    @media screen and (min-width: $on-large) {
      max-width: calc(#{$content-width} - (#{$spacing-unit} * 2));
      padding-right: $spacing-unit;
      padding-left: $spacing-unit;
    }
  }
  
  
  
  /**
   * Clearfix
   */
  %clearfix:after {
    content: "";
    display: table;
    clear: both;
  }
  
  
  
  /**
   * Icons
   */
  
  .orange {
    color: #f66a0a;
  }
  
  .grey {
    color: #828282;
  }

  .brand-color { //custom
    color: $brand-color; 
  }
  
  .svg-icon {
    width: 16px;
    height: 16px;
    display: inline-block;
    fill: currentColor;
    padding: 5px 3px 3px 5px;
    vertical-align: text-bottom;
  }
  
  
  /**
   * Tables
   */
  table {
    margin-bottom: 0px; //$spacing-unit
    width: 100%;
    overflow-x: auto; 
    text-align: $table-text-align;
    color: $table-text-color;
    border-collapse: collapse;
    border: 0px solid $table-border-color; //custom
    tr {
      &:nth-child(even) {
        background-color: $table-zebra-color;
      }
    }
    th, td {
      padding: 3px; // ($spacing-unit / 3) ($spacing-unit / 2)
    }
    th {
      background-color: $table-header-bg-color;
      border: 0px solid $table-header-border; //custom
    }
    td {
      border: 0px solid $table-border-color; //custom
    }
  
    @include media-query($on-laptop) {
      display: block;
      overflow-x: auto; 
      -webkit-overflow-scrolling: touch;
              -ms-overflow-style: -ms-autohiding-scrollbar;
    }
  }

//custom button
  button {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background: $code-background-color; 
    border: 0px solid #888;
    padding: 5px 10px;
    text-align: center;
    border-radius: 8px;
    text-decoration: none;
    display: inline-block center;
    font-size: $base-font-size;
    }

//custom: eliminate line spacing irregularities
sup {
  vertical-align: super; 
  line-height: 1ex;
}

sub {
  vertical-align: sub;
  line-height: 1ex;
}


//custom textarea
  textarea {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background: $background-color; 
    border: 0px solid #888; 
    outline: none;
    resize: none;
    overflow: hidden;
    width:100%;
    }
