/**
 * Site header
 */
 .site-header {
    border-top: 0px solid $border-color-01; //custom
    border-bottom: 1px solid $border-color-01; //custom
    min-height: $spacing-unit * 1.865;
    line-height: $base-line-height * $base-font-size * 2.25;
  
    // Positioning context for the mobile navigation icon
    position: relative;
  }
  
  .site-title {
    @include relative-font-size(1.625);
    font-weight: 400; //default 300
    letter-spacing: -1px;
    margin-bottom: 0;
    float: left;
  
    @include media-query($on-palm) {
      padding-right: 45px;
    }
  
    &,
    &:visited {
      color: $site-title-color;
    }
  }
  
  .site-nav {
    position: absolute;
    top: 9px;
    right: $spacing-unit / 2;
    background-color: $background-color;
    border: 0px solid $border-color-01; //default 1px
    border-radius: 5px;
    text-align: right;
  
    .nav-trigger {
      display: none;
    }
  
    .menu-icon {
      float: right;
      width: 36px;
      height: 26px;
      line-height: 0;
      padding-top: 10px;
      text-align: center;
  
      > svg path {
        fill: $border-color-03;
      }
    }
  
    label[for="nav-trigger"] {
      display: block;
      float: right;
      width: 36px;
      height: 36px;
      z-index: 2;
      cursor: pointer;
    }
  
    input ~ .trigger {
      clear: both;
      display: none;
    }
  
    input:checked ~ .trigger {
      display: block;
      padding-bottom: 5px;
      background-color: $brand-color-darker;
    }
  
    .page-link {
      color: $text-color;
      line-height: $base-line-height;
      display: block;
      font-size: 16pt; //custom
      padding: 10px 15px; //custom
  
      // Gaps between nav items, but not on the last one
      &:not(:last-child) {
        margin-right: 00px;
      }
      margin-left: 20px;
    }
  
    @media screen and (min-width: $on-medium) {
      position: static;
      float: right;
      border: none;
      background-color: inherit;
  
      label[for="nav-trigger"] {
        display: none;
      }
  
      .menu-icon {
        display: none;
      }
  
      input ~ .trigger {
        display: block;
      }
  
      .page-link {
        display: inline;
        padding: 0;
        font-size: $base-font-size;
        padding: 5px 10px; //custom
  
        &:not(:last-child) {
          margin-right: 20px;
        }
        margin-left: auto;
      }
    }
  }
  
  
  
  /**
   * Site footer
   */
  .site-footer {
    border-top: 1px solid $border-color-01; //custom
    padding: $spacing-unit 0;
  }
  
  .footer-heading {
    @include relative-font-size(1.125);
    margin-bottom: $spacing-unit / 2;
  }
  
  .feed-subscribe .svg-icon {
    padding: 5px 5px 2px 0
  }
  
  .contact-list,
  .social-media-list {
    list-style: none;
    margin-left: 0;
  }
  
  .footer-col-wrapper,
  .social-links {
    @include relative-font-size(0.9375);
    color: $brand-color;
  }
  
  .footer-col {
    margin-bottom: $spacing-unit / 2;
    text-align: center; //custom
  }
  
  .footer-col-1 {
    width: calc(100% - (#{$spacing-unit} / 2));
  }
  .footer-col-2 {
    width: calc(100% - (#{$spacing-unit} / 2)); //custom
  }
    
  @media screen and (min-width: $on-medium) {
    .footer-col-wrapper {
      display: flex
    }
  
    .footer-col {
      padding: 0 ($spacing-unit / 2);
  
      &:first-child {
        text-align: left; //custom
        width: calc(50% - (#{$spacing-unit} / 2));
        padding-right: $spacing-unit / 2;
        padding-left: 0;
      }
  
      &:last-child {
        text-align: right; // align col2 on large screens to the right
        width: calc(50% - (#{$spacing-unit} / 2));
        padding-right: 0;
        padding-left: $spacing-unit / 2;
      }
    }
  }
  
  
  
  /**
   * Page content
   */
  .page-content {
    padding: $spacing-unit 0;
    flex: 1 0 auto;
  }
  
  .page-heading {
    @include relative-font-size(2);
  }
  
  .post-list-heading {
    @include relative-font-size(1.75);
  }
  
  .post-list {
    margin-left: 0;
    list-style: none;
  
    > li {
      margin-bottom: $spacing-unit;
    }
  }
  
  .post-meta {
    font-size: $small-font-size;
    color: $brand-color;
  }
  
  .post-link {
    display: block;
    @include relative-font-size(1.5);
  }
  
  
  
  /**
   * Posts
   */
  .post-header {
    margin-bottom: $spacing-unit;
  }
  
  .post-title,
  .post-content h1 {
    @include relative-font-size(2.625);
    letter-spacing: -1px;
    line-height: 1.15;
  
    @media screen and (min-width: $on-large) {
      @include relative-font-size(2.625);
    }
  }
  

  .post-content {
    margin-bottom: $spacing-unit;
  
    h1, h2, h3 { margin-top: $spacing-unit * 2 }
    h4, h5, h6 { margin-top: $spacing-unit }
  
    h2 {
      @include relative-font-size(1.75);
  
      @media screen and (min-width: $on-large) {
        @include relative-font-size(2);
      }
    }
  
    h3 {
      @include relative-font-size(1.375);
  
      @media screen and (min-width: $on-large) {
        @include relative-font-size(1.625);
      }
    }
  
    h4 {
      @include relative-font-size(1.25);
    }
  
    h5 {
      @include relative-font-size(1.125);
    }
    h6 {
      @include relative-font-size(1.0625);
    }
    /* custom justified textbody 
    text-align: justify;*/
