  /**
   * Pagination navbar
   */
  .pagination {
    display: flex;
    justify-content: center;
    a{
      color: $text-color;
      float: left;
      padding: 8px 16px;
      text-decoration: none;
    }
    span{
      color: $brand-color-dark;
      float: left;
      padding: 8px 16px;
      text-decoration: none; 
    }
    span.here{
      color: $brand-color-dark;
      float: left;
      padding: 8px 16px;
      text-decoration: none; 
    }
    a.active {
      background-color: $link-hover-color;
      color: $brand-color-dark;
      border-radius: 5px;
    }
    a:hover:not(.active) {background-color: $brand-color-light;color:$brand-color-dark;}
      div {
        color: $text-color;
        min-width: 41px;
        text-align: center;
        box-sizing: border-box;
      }
  }
  
  
  
  
  /**
   * Grid helpers
   */
  @media screen and (min-width: $on-large) {
    .one-half {
      width: calc(50% - (#{$spacing-unit} / 2));
    }
  }


// custom print layout
@media print {
  body {
    color: #000;
    background: #fff;
    zoom: 80%;
  }
  .site-nav, .site-title, .site-footer {
    display: none;
  }
  .post-content {
    h4 { 
      page-break-inside:avoid; 
    }
    img { 
      page-break-inside: avoid; 
      filter: invert(0);
      background-color: #fff;
    }
    p {
      display: block;
      page-break-inside: avoid;
    }
    blockquote {
      page-break-inside: avoid;
    }
  }
  .site-header:before {
    content: "the suboptimal printing layout of davwi.de is your problem";
    color: #777 !important;
    font-size: 1em;
    font-style: italic;
    padding-top: 10px;
    text-align: center !important;    
  }
}

/*Scrollbar Styling*/

/* W3C standard */
* {
  scrollbar-width: thin;
  scrollbar-color: $brand-color-dark $background-color; /* #A9F5D0 #80bfff */
}
/* webkit */
*::-webkit-scrollbar {
  width: 10px;
}
*::-webkit-scrollbar-track {
  background: $background-color;
}
*::-webkit-scrollbar-thumb {
  background-color: $brand-color-dark;
  border-radius: 20px;
  border: 3px solid $brand-color-dark;
}

